import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';


const routes: Routes = [
    {
        path: 'complaints',
        loadChildren: () => import('./complaints/complaints.module').then(m => m.ComplaintsModule),
    },
    {
        path: 'healthcare',
        loadChildren: () => import('./healthcare/healthcare.module').then(mod => mod.HealthcareModule)
    },
    {
        path: 'uniform',
        loadChildren: () => import('./uniform/uniform.module').then(mod => mod.UniformModule)
    },
];

@NgModule({
    imports: [RouterModule.forChild(routes)],
    exports: [RouterModule]
})
export class HelpdeskRoutingModule { }
